
import Vue from 'vue'
import { editAddress, getAddressDetail, setUseAddress } from '@/api/address.js'
import { pca, pcaa } from 'area-data';
import VueAreaLinkage from 'vue-area-linkage';
import 'vue-area-linkage/dist/index.css';
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'

Vue.use(VueAreaLinkage)
export default {
components: {
XButton,
XInput,
Group,
XAddress,
pca,
pcaa
},
data () {
return {
selected2:[],
pcaa:pcaa,
addressData: ChinaAddressData,
addressValue: ['', '', ''],
getaddres: [],
id: '',
postData: {
name: '',
mob: '',
address: ''
}
}
},
methods: {
addressbtn: function () {
if (this.postData.name === '') {
this.$vux.toast.text('请填写收货人姓名', 'middle')
return
}

const reg = 11 && /^((13|14|15|16|17|18|19)[0-9]{1}\d{8})$/
if (!reg.test(this.postData.mob)) {
if (this.postData.mob === '' || this.postData.mob === null) {
this.$vux.toast.text('请填写手机号', 'middle')
return
}
this.$vux.toast.text('手机号有误')
return
}
console.log(this.selected2)
if (this.selected2 === '请选择' || this.selected2.length===0) {
this.$vux.toast.text('请选择城市', 'middle')
return
}
if (this.postData.address === ''|| this.postData.address === undefined) {
this.$vux.toast.text('请填写收货地址', 'middle')
return
}
const _this = this
_this.postData.province = _this.selected2[0]
_this.postData.city = _this.selected2[1]
_this.postData.district = _this.selected2[2]
_this.postData.isdefault = 0
_this.postData.id = _this.id
const postData = _this.postData
editAddress(postData)
.then(res => {
if (res.data.errorcode === 0) {

this.$vux.toast.text('保存成功', 'middle')
if (localStorage.getItem('beforeConfirmPath')) {
var from = window.localStorage.getItem('isFromOrder')
if (from != 0) {
const postData = {
id: res.data.data.addressid
}
setUseAddress(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.$router.push({path: '/confirmOrder', query: { id: res.data.data.addressid, preid: from}})
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
this.$router.push('address')
})
}
return
}
this.$router.push('address')
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
},
onShadowChange (ids, names) {
this.getaddres = names
},
AddressList () {
const postData = {
id: this.id
}
const that = this
getAddressDetail(postData)
.then(res => {
if (res.data.errorcode === 0) {
that.postData.name = res.data.data.name
that.postData.mob = res.data.data.mob
that.postData.address = res.data.data.address
that.selected2 = [res.data.data.province, res.data.data.city, res.data.data.district]
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
}
},
created () {
this.id = this.$route.query.id
if (this.id === '' || this.id === undefined) {

} else {
this.AddressList()
}
}
}



